.root {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.8em;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.rootVertical {
  composes: root;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.rootHorizontal {
  composes: root;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.label {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  margin-bottom: 0.4em;
}

.value {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 0px;
          flex: 0 0 0;
  width: 100%;
}

.barContainer {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  width: 100%;
  margin-bottom: 0.2em;
  cursor: ns-resize;
}

.track,
.bar {
  position: absolute;
  top: 0;
  bottom: 0;
}

.track {
  z-index: 1;
  width: 1px;
  left: 50%;
  background-color: rgba(0, 0, 0, 0.1);
}

.bar {
  z-index: 2;
  width: 3px;
  left: calc(50% - 1px);
  background-color: #000;
}



